export default {
	data() {
		return {
			form:{
				name: '',
				tel: '',
			}
		};
	},
	onNavigationBarButtonTap(res){
		this.register();
	},
	methods: {
		isPhone(e) {
			console.log(JSON.stringify(e));
			let phone = e;
			var myreg = /^[1][3,4,5,6,7,8,9][0-9]{9}$/;
			if (myreg.test(phone) === false) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号'
				});
				return false;
			}
			return true;
		},
		register() {
			const data = {
				name: this.name,
				tel: this.tel,
			};
			if(!this.form.name){
				uni.showToast({
					icon:'none',
					title: '联系人不能为空'
				});
				return;
			}
			const isphone =  this.isPhone(this.form.tel);
			
		}
	}
};
